import React, { Component } from 'react'
import './navigationbar.css'
import {navbar} from './nb'
import ta from "./images/ta1.jpg";



 class Navigationbar extends Component {
   
    componentDidMount() {
        navbar();
      }
    

    render() {
        return (
            <React.Fragment>
                
                   
                        <div class="nav0">
                            <img src={ta} class="ta"></img>
                            <nav class="nav">                            
                               <a href="#" class="nav-item is-active" active-color="orange" >Home</a>
                               <a href="#" class="nav-item" active-color="green">Subjects</a>
                               <a href="#" class="nav-item" active-color="blue">Participants</a>
                               <span class="nav-indicator"></span>
                             </nav>
                        </div>
                   
            </React.Fragment>
           
        )
    }
}

export default Navigationbar
