import React, { Component } from 'react'
import './home.css'
import bg0 from "./images/bg2.gif";
import bg1 from "./images/ta1.png";
import bg2 from "./images/book.png";
import {scrollFunction} from './hm'
import ReactAudioPlayer from 'react-audio-player';

 class Home extends Component {
    componentDidMount() {
        scrollFunction();
        
            const script = document.createElement("script");
            script.src = "https://kit.fontawesome.com/a076d05399.js";
            script.async = true;
            document.body.appendChild(script);
         
      };
      playAudio(){
        const audioEl = document.getElementsByClassName("audio-element")[0];
        audioEl.play();
      };
    render() {
        return (
               <React.Fragment>
                    <div class="bg">
                    <div class="container0">
                          <div class="cntr0" style={{fontFamily:"Lucida Bright"}} ><big style={{color:"red",fontFamily: "Herculanum"}}  >T</big>eaching <big style={{color:"#ff4314",fontFamily: "Herculanum"}}>A</big>ssistant!
                        <br /><small class="cntr1" style={{fontFamily:"Curlz MT"}} ><big style={{color:"red"}}>T</big>otally <big  style={{color:"red"}}>A</big>wesome</small></div>
                          <div class="overlay0">
                           <h1 class="text0"><big >I am a dedicated</big></h1>

                          </div> 
                         

                      </div> 
                      <img src={bg0} class="bg0" alt=""></img>
                      <img src={bg2} class="bg2" alt=""></img>
                  
                    </div>
                    <img src={bg1} id="bg1" alt=""></img>
                    <h1 class="text1"><big class="text10" >Teach </big><big class="text11" > Love </big><big class="text12" >Inspire</big></h1>

                    <a  id="gotopbtn" href="#" style={{fontSize:35 }}><i  class='far'>&#xf35b;</i></a>
                    <h4 class="gtbh">SCROLL TO TOP</h4>
                    <audio className="audio-element">
                       <source src="ta-app\src\components\audio\bg.mp3"></source>
                     </audio>
                     <button onClick={this.playAudio}>
          <span>Play Audio</span>
        </button>
        <ReactAudioPlayer
                     src="audio\bg.mp3"
                     autoPlay
                     controls
                   />
                </React.Fragment>
            )
        }
        
    }



export default Home

